.rci-dnd {
  display: flex;
}
.rci-dnd-block {
  flex: 1;
  border: 1px solid #ccc;
  padding: 10px;
  margin: 0 10px 0 0;
}
.rci-dnd-card {
  cursor: move;
  opacity: 1;
  border: 1px solid #ddd;
  margin: 10px 0;
}
.rci-dnd-card-dragging {
  opacity: 0;
}
.rci-dnd-card-over {
  background-color: #eee;
}
.rci-dnd-card-placeholder {
  height: 20px;
  background-color: #ccc;
}



